import React, { Component } from "react";
import {
  CCard,
  CCardBody,
  CCardGroup,
  CCardHeader,
  CBadge,
  CDataTable,
} from "@coreui/react";
import { connect } from "react-redux";
import usersData from "../users/UsersData";
const getBadge = (status) => {
  switch (status) {
    case "Active":
      return "success";
    case "Inactive":
      return "secondary";
    case "Pending":
      return "warning";
    case "Banned":
      return "danger";
    default:
      return "primary";
  }
};
const fields = [
  "Lead ID",
  "Date",
  "Time",
  "Rep Name",
  "Inventory",
  "Reservation",
  "Booking",
  "Granote ID",
  "Contacted by Rep",
  "Inbound Call",
  "Customer Name",
  "Phone",
  "Email",
  "From Address",
  "To Address",
  "Lead Type",
  "LP Source",
  "SMS Delay",
  "Msg Flow",
  "Recording",
  "Home Size",
  "Home Type",
  "Sq. Ft.",
  "Furnishing",
  "Price",
];

class ConversionManager extends Component {
  renderAdmin() {
    if (!this.props.isSignedIn) {
      return (
        <div>
          <h1>You Are Not Signed In</h1>
        </div>
      );
    }
    return (
      <CCardGroup>
        <CCard>
          <CCardHeader>Striped Table</CCardHeader>
          <CCardBody>
            <CDataTable
              items={usersData}
              fields={fields}
              striped
              itemsPerPage={5}
              pagination
              scopedSlots={{
                status: (item) => (
                  <td>
                    <CBadge color={getBadge(item.status)}>{item.status}</CBadge>
                  </td>
                ),
              }}
            />
          </CCardBody>
        </CCard>
      </CCardGroup>
    );
  }
  render() {
    return this.renderAdmin();
  }
}

const mapStateToProps = (state) => {
  return {
    isSignedIn: state.auth.isSignedIn,
  };
};

export default connect(mapStateToProps, {})(ConversionManager);
